import React from 'react';
import { Link } from 'react-router-dom';

const apps = [
  {
    title: "灰分計算機",
    description: "灰分含有量を計算するためのツール",
    link: "/ash-calculator"
  },
  // 他のアプリケーションをここに追加
  // 例:
  // {
  //   title: "別のアプリ",
  //   description: "別のアプリの説明",
  //   link: "/other-app"
  // },
];

function Home() {
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-4xl font-bold text-center mb-8">Web アプリケーション集</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {apps.map((app, index) => (
          <AppCard key={index} {...app} />
        ))}
      </div>
    </div>
  );
}

function AppCard({ title, description, link }) {
  return (
    <div className="bg-white shadow-md rounded-lg p-6">
      <h2 className="text-xl font-semibold mb-2">{title}</h2>
      <p className="text-gray-600 mb-4">{description}</p>
      <Link to={link} className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 transition duration-300">
        アプリを開く
      </Link>
    </div>
  );
}

export default Home;