import React, { useState, useRef, useEffect } from 'react';
import { PlusCircle, Upload, Download, HelpCircle, Trash2 } from 'lucide-react';
import { read, utils, write } from 'xlsx';

const AshContentCalculator = () => {
  const [samples, setSamples] = useState([
    { id: 1, name: 'サンプル 1', crucibleNumber: '', crucible: '', sample: '', ash: '', ashContent: null }
  ]);
  const [error, setError] = useState('');
  const [showGuide, setShowGuide] = useState(false);
  const fileInputRef = useRef(null);

  const calculateAshContent = (crucible, sample, ash) => {
    if (crucible === '' || sample === '' || ash === '') return null;
    const crucibleWeight = parseFloat(crucible);
    const sampleWeight = parseFloat(sample);
    const ashWeight = parseFloat(ash);
    if (isNaN(crucibleWeight) || isNaN(sampleWeight) || isNaN(ashWeight)) return null;
    if (ashWeight <= crucibleWeight || sampleWeight <= 0) return null;
    return ((ashWeight - crucibleWeight) / sampleWeight * 100).toFixed(2);
  };

  useEffect(() => {
    const newSamples = samples.map(sample => ({
      ...sample,
      ashContent: calculateAshContent(sample.crucible, sample.sample, sample.ash)
    }));
    setSamples(newSamples);
  }, [samples]);

  const handleInputChange = (id, field, value) => {
    const newSamples = samples.map(sample =>
      sample.id === id ? { ...sample, [field]: value } : sample
    );
    setSamples(newSamples);
    setError('');
  };

  const addSample = () => {
    const newId = Math.max(...samples.map(s => s.id), 0) + 1;
    setSamples([...samples, { id: newId, name: `サンプル ${newId}`, crucibleNumber: '', crucible: '', sample: '', ash: '', ashContent: null }]);
  };

  const removeSample = (id) => {
    setSamples(samples.filter(sample => sample.id !== id));
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();
    reader.onload = (e) => {
      const data = new Uint8Array(e.target.result);
      const workbook = read(data, { type: 'array' });
      const sheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[sheetName];
      const jsonData = utils.sheet_to_json(worksheet, { header: 1 });

      const headers = jsonData[0];
      const columnIndexes = {
        crucibleNumber: headers.indexOf('るつぼ番号'),
        name: headers.indexOf('サンプル名'),
        crucible: headers.indexOf('るつぼ重量'),
        sample: headers.indexOf('サンプル重量'),
        ash: headers.indexOf('灰化後重量'),
        ashContent: headers.indexOf('灰分')
      };

      const newSamples = jsonData.slice(1).map((row, index) => ({
        id: index + 1,
        crucibleNumber: row[columnIndexes.crucibleNumber] || '',
        name: row[columnIndexes.name] || `サンプル ${index + 1}`,
        crucible: row[columnIndexes.crucible] || '',
        sample: row[columnIndexes.sample] || '',
        ash: row[columnIndexes.ash] || '',
        ashContent: null
      }));

      setSamples(newSamples);
    };
    reader.readAsArrayBuffer(file);
  };

  const exportToExcel = () => {
    const workbook = utils.book_new();
    const worksheet = utils.json_to_sheet(samples.map(sample => ({
      'るつぼ番号': sample.crucibleNumber,
      'サンプル名': sample.name,
      'るつぼ重量': sample.crucible,
      'サンプル重量': sample.sample,
      '灰化後重量': sample.ash,
      '灰分 (%)': sample.ashContent
    })));
    utils.book_append_sheet(workbook, worksheet, '灰分データ');
    const excelBuffer = write(workbook, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    const url = URL.createObjectURL(data);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'ash_content_data.xlsx';
    link.click();
  };

  return (
    <div className="p-6 max-w-4xl mx-auto bg-gradient-to-br from-blue-50 to-indigo-100 rounded-lg shadow-lg">
      <div className="flex justify-between items-center mb-6">
        <h2 className="text-3xl font-bold text-indigo-800">灰分計算システム</h2>
        <div>
          <input
            type="file"
            ref={fileInputRef}
            style={{ display: 'none' }}
            onChange={handleFileUpload}
            accept=".csv,.xlsx,.xls"
          />
          <button 
            onClick={() => fileInputRef.current.click()} 
            className="bg-green-500 text-white p-3 rounded-full hover:bg-green-600 transition duration-300 ease-in-out mr-4"
            title="インポート"
          >
            <Upload size={24} />
          </button>
        </div>
      </div>
      
      {showGuide && (
        <div className="bg-yellow-100 border-l-4 border-yellow-500 text-yellow-700 p-4 mb-6">
          <h3 className="font-bold">使い方ガイド</h3>
          <p>1. サンプル情報を入力してください。</p>
          <p>2. 灰分は自動的に計算されます。</p>
          <p>3. ExcelまたはCSVファイルからデータをインポートすることもできます。</p>
          <p>4. 結果はExcelファイルとしてエクスポートできます。</p>
          <button onClick={() => setShowGuide(false)} className="mt-2 text-yellow-700 underline">閉じる</button>
        </div>
      )}
      
      <div className="space-y-6 mb-8">
        {samples.map((sample) => (
          <div key={sample.id} className="bg-white rounded-lg shadow-md transition duration-300 ease-in-out hover:shadow-lg p-6">
            <div className="flex justify-between mb-4">
              <input
                type="text"
                value={sample.name}
                onChange={(e) => handleInputChange(sample.id, 'name', e.target.value)}
                className="text-xl font-semibold text-indigo-700 border-b-2 border-indigo-200 focus:border-indigo-500 outline-none bg-transparent"
              />
              <div className="flex items-center">
                <label className="mr-2 text-indigo-600">るつぼ番号:</label>
                <input
                  type="text"
                  value={sample.crucibleNumber}
                  onChange={(e) => handleInputChange(sample.id, 'crucibleNumber', e.target.value)}
                  className="border-b-2 border-indigo-200 focus:border-indigo-500 outline-none bg-transparent"
                />
                <button 
                  onClick={() => removeSample(sample.id)} 
                  className="ml-4 text-red-500 hover:text-red-700 transition duration-300 ease-in-out"
                  title="サンプル削除"
                >
                  <Trash2 size={20} />
                </button>
              </div>
            </div>
            <div className="grid grid-cols-2 gap-6">
              <div>
                <label className="block mb-2 text-indigo-600">るつぼ重量 (g)</label>
                <input
                  type="number"
                  value={sample.crucible}
                  onChange={(e) => handleInputChange(sample.id, 'crucible', e.target.value)}
                  className="w-full p-2 border border-indigo-200 rounded focus:border-indigo-500 outline-none"
                />
              </div>
              <div>
                <label className="block mb-2 text-indigo-600">サンプル重量 (g)</label>
                <input
                  type="number"
                  value={sample.sample}
                  onChange={(e) => handleInputChange(sample.id, 'sample', e.target.value)}
                  className="w-full p-2 border border-indigo-200 rounded focus:border-indigo-500 outline-none"
                />
              </div>
              <div>
                <label className="block mb-2 text-indigo-600">灰化後重量 (g)</label>
                <input
                  type="number"
                  value={sample.ash}
                  onChange={(e) => handleInputChange(sample.id, 'ash', e.target.value)}
                  className="w-full p-2 border border-indigo-200 rounded focus:border-indigo-500 outline-none"
                />
              </div>
              <div>
                <label className="block mb-2 text-indigo-600">灰分 (%)</label>
                <input
                  type="text"
                  value={sample.ashContent !== null ? sample.ashContent : ''}
                  readOnly
                  className="w-full p-2 bg-indigo-50 border border-indigo-200 rounded text-indigo-700 font-semibold"
                />
              </div>
            </div>
          </div>
        ))}
      </div>
      
      {error && <p className="text-red-500 mt-4 mb-4">{error}</p>}

      <div className="flex justify-between items-center mt-6">
        <button 
          onClick={addSample} 
          className="bg-indigo-500 text-white p-3 rounded-full hover:bg-indigo-600 transition duration-300 ease-in-out"
          title="サンプル追加"
        >
          <PlusCircle size={24} />
        </button>
        <button 
          onClick={exportToExcel} 
          className="bg-blue-500 text-white p-3 rounded-full hover:bg-blue-600 transition duration-300 ease-in-out"
          title="エクスポート"
        >
          <Download size={24} />
        </button>
      </div>

      <div className="bg-white rounded-lg shadow-md p-6 mt-8">
        <h3 className="text-xl font-semibold text-indigo-700 mb-4">計算式</h3>
        <p className="text-indigo-600 mb-4">
          灰分 (%) = [(灰化後重量 - るつぼ重量) / サンプル重量] × 100
        </p>
      </div>

      <button 
        onClick={() => setShowGuide(true)} 
        className="fixed bottom-4 right-4 bg-indigo-500 text-white p-3 rounded-full hover:bg-indigo-600 transition duration-300 ease-in-out"
        title="ヘルプ"
      >
        <HelpCircle size={24} />
      </button>
    </div>
  );
};

export default AshContentCalculator;